const Wrapper = styled.div`
  width: 100%;
  padding: 20px;
  min-height: 200px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: flex-start;
`;

const TriggerButton = styled.button`
  background: linear-gradient(135deg, #6366f1 0%, #4f46e5 100%);
  color: white;
  font-weight: 600;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.3);

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(99, 102, 241, 0.4);
  }

  &:active {
    transform: translateY(0);
  }
`;

const meta: Meta<typeof ModalConfirm> = {
  title: 'Components/Modal/ModalConfirm',
  component: ModalConfirm,
  tags: ['autodocs'],
  parameters: {
    componentSubtitle: `import { ModalConfirm } from 'react-restyle-components'`,
    docs: {
      description: {
        component: `
A confirmation modal component for user actions that require confirmation.

## Features
- ‚úÖ Built-in confirmation dialog UI
- üé¨ Smooth open/close animations
- üì± Fully responsive design
- üéØ Customizable title and message
- üîò Configurable submit and close button labels
- ‚ôø Accessible with proper ARIA attributes
- üé® Customizable styling with classNames and styles props
- ‚öôÔ∏è Control over click and close behavior
- ‚è±Ô∏è Configurable animation duration

## Basic Usage
\`\`\`tsx
import { ModalConfirm } from 'react-restyle-components';
import { useState } from 'react';

const [visible, setVisible] = useState(false);

<button onClick={() => setVisible(true)}>Show Confirmation</button>
<ModalConfirm
  visible={visible}
  title="Confirm"
  message="Are you sure you want to delete this item?"
  submitTitle="Delete"
  closeTitle="Cancel"
  animationDuration={200}
  onClick={() => {
    // Handle confirmation
    setVisible(false);
  }}
  onClose={() => setVisible(false)}
/>
\`\`\`
        `,
      },
    },
    layout: 'centered',
  },
  decorators: [
    (Story) => (
      <Wrapper>
        <Story />
      </Wrapper>
    ),
  ],
};

export default meta;
